import React from 'react'
import Link from 'gatsby-link'
import styled, { keyframes } from 'styled-components'
import Empty from '../assets/cursor/empty.svg'
import Filled from '../assets/cursor/filled.svg'
import {start, Start, rainbow, StyledText, Content, ContentGroup, Parent, Sub, Body, StyledLink, Glow, BodyImage} from '../components/styles.js'

class Yards extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      hover1: false,
      hover2: false,
      hover3: false,
      hover4: false
    }
  }

  mouseOver = (p) => {
    this.setState({[p]: true})
  }

  mouseOut = (p) => {
    this.setState({[p]: false})
  }

  render() {
    return (
      <div>
        <Start>
          <Content>
            <ContentGroup>
              <p>
                <Glow>
                Yards
                </Glow>

                <Sub>
                Jun 2020 - Aug 2020
                </Sub>

                <Body>
                YARDS is a command-line interface written in Python that efficiently produces semi-realistic, retro-game sprite detection datasets without manual labeling.
                Provided with sprites, background images, and a set of parameters, the package uses sprite frequency spaces to create synthetic gameplay images along with their corresponding labels.
                YARDS was the result of countless frustrating attempts to train neural networks for sprite detection in video games and an insufficient supply of training images to train said models.
                It was developed over the summer while working as a research assistant in a professor's lab.
                I (along with Chanha Kim) presented a workshop at AIIDE 2020 covering our research done over the summer and got a paper published through the conference.
                </Body>

                <Body>
                For more information, check out the <StyledLink href='https://github.com/faimSD/yards' target='_blank'>GitHub repository</StyledLink>. And to read the published paper, <StyledLink href='http://www.exag.org/papers/EXAG_2020_paper_8.pdf' target='_blank'>click here</StyledLink>.
                </Body>
              </p>
            </ContentGroup>
          </Content>
        </Start>
      </div>
    )
  }
}

export default Yards



